'use client';

import { useEffect, useState, useRef } from 'react';
import { useKontext } from './context';
import { useQueryClient } from '@tanstack/react-query';
import { createTRPCClient, createWSClient, splitLink, wsLink, httpBatchLink } from '@trpc/client';
import superjson from 'superjson';
import type { AppRouter } from '../types/api-types';

export interface RealtimeContextOptions {
  enabled?: boolean;
  onUpdate?: (event: any) => void;
}

export interface RealtimeContextState {
  isConnected: boolean;
  lastUpdate: Date | null;
  updateCount: number;
  recentUpdates: Array<{
    type: string;
    timestamp: Date;
    metadata?: any;
  }>;
}

/**
 * Hook for real-time context updates via WebSocket subscriptions
 */
export function useRealtimeContext(options: RealtimeContextOptions = {}) {
  const { userId, apiKey } = useKontext();
  const queryClient = useQueryClient();
  const [state, setState] = useState<RealtimeContextState>({
    isConnected: false,
    lastUpdate: null,
    updateCount: 0,
    recentUpdates: [],
  });

  const { enabled = true, onUpdate } = options;

  // Use ref to store the callback to avoid dependency issues
  const onUpdateRef = useRef(onUpdate);
  onUpdateRef.current = onUpdate;

  // Store cleanup flag to prevent state updates after unmount
  const isMountedRef = useRef(true);

  useEffect(() => {
    isMountedRef.current = true;

    if (!enabled || !userId || !apiKey) {
      return;
    }

    let isCleanedUp = false;

    // Parse API URL to get WebSocket URL
    const apiUrl = process.env.NEXT_PUBLIC_KONTEXT_API_URL || 'https://api.kontext.dev';
    const wsUrl = apiUrl.replace('https://', 'wss://').replace('http://', 'ws://');

    // Create WebSocket client
    const wsClient = createWSClient({
      url: `${wsUrl}/trpc`,
      connectionParams: async () => ({
        'x-api-key': apiKey,
      }),
    });

    // Create tRPC client with WebSocket support
    const trpcClient = createTRPCClient<AppRouter>({
      links: [
        splitLink({
          condition: (op) => op.type === 'subscription',
          true: wsLink({
            client: wsClient,
            transformer: superjson,
          }),
          false: httpBatchLink({
            url: `${apiUrl}/trpc`,
            headers: {
              'x-api-key': apiKey,
            },
            transformer: superjson,
          }),
        }),
      ],
    });

    // Subscribe to context updates
    let subscription: any;
    try {
      subscription = (trpcClient as any).data.contextUpdates.subscribe(
        { userId },
        {
          onData: (event: any) => {
            if (isCleanedUp || !isMountedRef.current) return;

            console.log('Received context update:', event);

            // Update state - ensure timestamp is properly handled
            setState((prev) => ({
              isConnected: true,
              lastUpdate: new Date(),
              updateCount: prev.updateCount + 1,
              recentUpdates: [
                {
                  type: event.type || 'unknown',
                  timestamp: event.timestamp ? new Date(event.timestamp) : new Date(),
                  metadata: event.metadata || {},
                },
                ...prev.recentUpdates.slice(0, 9), // Keep last 10 updates
              ],
            }));

            // Invalidate context queries to trigger refetch
            queryClient.invalidateQueries({ queryKey: ['context', userId] });

            // Call user callback if provided
            if (onUpdateRef.current) {
              onUpdateRef.current(event);
            }
          },
          onError: (error: any) => {
            if (isCleanedUp || !isMountedRef.current) return;

            console.error('Subscription error:', error);
            setState((prev) => ({
              ...prev,
              isConnected: false,
            }));
          },
        },
      );
    } catch (error) {
      console.error('Failed to create subscription:', error);
      return;
    }

    // Mark as connected
    if (!isCleanedUp && isMountedRef.current) {
      setState((prev) => ({
        ...prev,
        isConnected: true,
      }));
    }

    // Cleanup
    return () => {
      isCleanedUp = true;
      isMountedRef.current = false;
      if (subscription) {
        subscription.unsubscribe();
      }
      wsClient.close();
      // Don't update state in cleanup to avoid issues
    };
  }, [enabled, userId, apiKey, queryClient]);

  return state;
}

/**
 * Hook for automatic polling fallback when WebSocket is not available
 */
export function usePollingContext(intervalMs: number = 30000) {
  const { userId, isConnected } = useKontext();
  const queryClient = useQueryClient();
  const [isPolling, setIsPolling] = useState(false);

  useEffect(() => {
    if (!isConnected || !userId) {
      return;
    }

    setIsPolling(true);
    const interval = setInterval(() => {
      // Invalidate context to trigger refetch
      queryClient.invalidateQueries({ queryKey: ['context', userId] });
    }, intervalMs);

    return () => {
      setIsPolling(false);
      clearInterval(interval);
    };
  }, [isConnected, userId, intervalMs, queryClient]);

  return { isPolling };
}
