import type { PersonaConfig, GetContextOptions, PersonaError, Context } from './types';
import { createTRPCClient } from './trpc';

/**
 * Main Persona SDK client
 * Provides a simple API for developers to get personalized context
 */
export class Persona {
  private config: Required<PersonaConfig>;

  constructor(config: PersonaConfig) {
    // Validate that API key is provided
    if (!config.apiKey) {
      throw new Error('Persona API key is required');
    }

    // Set defaults
    this.config = {
      apiKey: config.apiKey,
      providers: config.providers || ['gmail', 'preferences'],
      apiUrl: config.apiUrl || 'https://api.kontext.dev',
      cache: {
        ttl: config.cache?.ttl || 300,
        storage: config.cache?.storage || 'memory',
      },
      limits: {
        maxTokensPerRequest: config.limits?.maxTokensPerRequest || 2000,
        monthlyBudget: config.limits?.monthlyBudget || 0,
      },
    };
  }

  /**
   * Get personalized context for a user
   * This is the main method developers will use
   */
  async getContext(options: GetContextOptions): Promise<Context> {
    // Validate input options
    if (!options) {
      throw this.createError(
        'GetContextOptions is required. Please provide an object with at least a userId field.',
        'INVALID_OPTIONS',
        400,
      );
    }

    const { userId, task, maxTokens, privacyLevel } = options;

    // Validate userId is provided and is a string
    if (!userId || typeof userId !== 'string') {
      throw this.createError(
        'Valid userId is required. Please provide a non-empty string userId.',
        'INVALID_USER_ID',
        400,
      );
    }

    // Validate maxTokens if provided
    if (maxTokens !== undefined && (typeof maxTokens !== 'number' || maxTokens <= 0)) {
      throw this.createError(
        'maxTokens must be a positive number if provided.',
        'INVALID_MAX_TOKENS',
        400,
      );
    }

    // Validate privacyLevel if provided
    if (privacyLevel && !['strict', 'moderate', 'none'].includes(privacyLevel)) {
      throw this.createError(
        'privacyLevel must be one of: strict, moderate, none',
        'INVALID_PRIVACY_LEVEL',
        400,
      );
    }

    // Create tRPC client with API key
    const authenticatedTrpc = createTRPCClient(this.config.apiUrl, this.config.apiKey);

    try {
      // Call backend context endpoint with userId
      const response = await (authenticatedTrpc as any).data.context.query({
        userId, // Pass userId as part of the query
        task: task || 'general',
        maxFacts: maxTokens ? Math.min(maxTokens / 10, 100) : undefined, // Rough conversion from tokens to facts
        cachePolicy: 'fresh', // Changed from 'cached' to always get fresh context
        includeRecentData: true,
        privacyLevel: privacyLevel || 'none',
      });

      // Transform response to match SDK Context interface
      // The API returns more data than the SDK exposes, so we simplify it
      return {
        systemPrompt: response.systemPrompt,
        metadata: {
          userId: options.userId,
          timestamp: response.metadata?.generatedAt || new Date(),
          providers: ['gmail'], // TODO: Track actual providers used
        },
        // Token count would need to be calculated from systemPrompt length
        tokenCount: response.systemPrompt.length / 4, // Rough estimate: 1 token â‰ˆ 4 chars
      };
    } catch (error) {
      // Provide more specific error messages based on the error type
      const message = error instanceof Error ? error.message : 'Failed to get context';

      // Check for common tRPC/API errors
      if (message.includes('Invalid input') || message.includes('expected object')) {
        throw this.createError(
          'Invalid request format. This may be a SDK version mismatch with the API.',
          'INVALID_REQUEST_FORMAT',
          400,
        );
      }

      if (message.includes('OAuth access') || message.includes('No OAuth')) {
        throw this.createError(
          'User has not authorized access. Please ensure the user has connected their account.',
          'UNAUTHORIZED_USER',
          403,
        );
      }

      // Generic error fallback
      throw this.createError(message, 'CONTEXT_FAILED', 500);
    }
  }

  /**
   * Disconnect user and delete their data
   * This calls the GDPR deleteData endpoint
   */
  async disconnect(userId: string): Promise<void> {
    // Validate userId is provided and is a string
    if (!userId || typeof userId !== 'string') {
      throw this.createError(
        'Valid userId is required for disconnect. Please provide a non-empty string userId.',
        'INVALID_USER_ID',
        400,
      );
    }

    try {
      // Create tRPC client with API key
      const authenticatedTrpc = createTRPCClient(this.config.apiUrl, this.config.apiKey);

      // Call GDPR endpoint with userId
      await (authenticatedTrpc as any).gdpr.deleteData.mutate({ userId });
    } catch (error) {
      // Provide more specific error messages based on the error type
      const message = error instanceof Error ? error.message : 'Failed to disconnect';

      // Check for specific API errors
      if (message.includes('not found') || message.includes('does not exist')) {
        throw this.createError(
          'User not found. The user may have already been disconnected.',
          'USER_NOT_FOUND',
          404,
        );
      }

      // Generic error fallback
      throw this.createError(message, 'DISCONNECT_FAILED', 500);
    }
  }

  /**
   * Create a PersonaError
   */
  private createError(message: string, code: string, statusCode?: number): PersonaError {
    const error = new Error(message) as PersonaError;
    error.name = 'PersonaError';
    error.code = code;
    error.statusCode = statusCode;
    return error;
  }
}
