import { createTRPCProxyClient, httpBatchLink } from '@trpc/client';
import superjson from 'superjson';

/**
 * Creates a tRPC client for the Kontext SDK
 *
 * @param apiUrl - The API endpoint URL
 * @param apiKeyOrToken - API key (ktext_*) or session token for authentication
 */
export function createTRPCClient(apiUrl: string, apiKeyOrToken: string) {
  // Check if this is an API key (starts with ktext) or a session token
  const isApiKey = apiKeyOrToken.startsWith('ktext');

  // Use 'any' type here to avoid internal dependency issues
  // The SDK will have proper type safety through the wrapper client class
  return createTRPCProxyClient<any>({
    links: [
      httpBatchLink({
        url: `${apiUrl}/trpc`,
        transformer: superjson,
        maxItems: 10, // Maximum number of operations in a single batch
        maxURLLength: 2083, // Prevent URLs from becoming too long
        async headers() {
          if (isApiKey) {
            // Use x-api-key header for API key authentication
            return {
              'x-api-key': apiKeyOrToken,
            };
          } else {
            // For session tokens, use Authorization Bearer format
            return {
              Authorization: `Bearer ${apiKeyOrToken}`,
            };
          }
        },
        fetch(url, options) {
          return fetch(url, {
            ...options,
            // Add connection pooling for better performance
            keepalive: true,
          });
        },
      }),
    ],
  });
}

export type KontextTRPCClient = ReturnType<typeof createTRPCClient>;
